<template>
	<div class="ecom-element ecom-shopify ecom-shopify__menu" >
		<div class="ecom-element ecom-shopify ecom-shopify__menu-wrapper" :data-show-all="show_all_items_menu??false">
			<div
				class="ecom-element ecom-shopify ecom-shopify__menu-container"
				v-html="liquid('linklists')"
			/>
		</div>
	</div>
</template>

<script>
	import { Liquid, Element, Javascript } from '@/mixins';
	export default {
		name: 'ShopifyMenu',
		mixins: [Liquid, Element, Javascript],
		props: {
			data: {
				type: Object,
				default () {
					return {};
				}
			}
		},
		data(){
			return {
				jsreactives: [
					"show_all_items_menu"
				]
			}
		},
		computed: {

			liquids () {
				return {
					linklists: {
						code: `
							{%- capture link_handle -%}${this.link_list ? this.link_list : ''}{%- endcapture -%}
							{%- unless link_handle == empty -%}
								{%- assign menu = linklists[link_handle] -%}
								{%- capture show_humber-%}${this.data.settings?.show_humber ? this.data.settings?.show_humber : false}{%- endcapture -%}
								{%- if show_humber == 'true' -%}
                                    <div class="ecom-menu__icon-humber--wrapper">
                                        <div class="ecom-menu__icon-humber">
                                            ${this.data.settings?.icon_humber?.value ? this.data.settings?.icon_humber?.value : ''}
                                        </div>
                                    </div>
									<div class="ecom-shopify__menu-list--mobile--wrapper">
										<ul class="ecom-shopify__menu-list--mobile" data-show-humber="${this.data.settings?.show_humber}">
											<div class="modal-header">
												<span class="ecom-menu-collapse-close--mobile">
													<svg xmlns="http://www.w3.org/2000/svg" class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12"></path></svg>
												</span>
											</div>
											{% if menu and menu.links.size %}
												{% for link in menu.links %}
													{% assign child_link = link.links %}
													<li class="ecom-shopify__menu-item ecom-shopify__menu-item-type--{{link.type}} {% if child_link.size > 0 %}ecom-shopify__menu-item--has-children{% endif %} ">
														<div class="ecom-menu_item ecom-items">
															<a class="ecom-menu_title ecom-items--text ecom-element--menu_title {% if link.active or link.child_active %} ecom-shopify__menu-item--active ecom-text-active {% endif %}" href="{{link.url}}" title="{{link.title | escape }}">
																{{link.title}}
																{%- if link.levels > 0 -%}
																	<div class="ecom-element--menu_icon">
																		<div class="ecom-items--icon ecom-element--menu_icon--normal">
																			${this.icon_menu ? this.icon_menu.value : ''}
																		</div>
																		<div class="ecom-items--icon ecom-element--menu_icon--active">
																			${this.icon_menu_active ? this.icon_menu_active.value : ''}
																		</div>
																	</div>
																{%- endif -%}
															</a>
														</div>
														{% if child_link.size > 0 %}
															<ul class="ecom-shopify__menu-sub-menu" data-menu-size="{{child_link.size}}" data-menu-level="{{child_link.level}}">
																{% for child in child_link %}
																	{% assign grand_link = child.links %}
																	<li class="ecom-shopify__menu-child-link-item ecom-shopify__menu-child-link-item-type--{{child.type}} {% if grand_link.size > 0 %}ecom-shopify__menu-child-link-item--has-children{% endif %} {% if child.active or child.child_active%} ecom-shopify__menu-child-link-item--active ecom-text-active{% endif %}" >
																		<div class="ecom-menu_item ecom-items">
																			<a class="ecom-menu_title ecom-items--text ecom-element--menu_title" href="{{child.url}}" title="{{child.title | escape }}">
																				{{child.title}}
																				{%- if child.levels > 0 -%}
																					<div class="ecom-element--menu_icon">
																						<div class="ecom-items--icon ecom-element--menu_icon--normal">
																							${this.icon_menu ? this.icon_menu.value : ''}
																						</div>
																						<div class="ecom-items--icon ecom-element--menu_icon--active">
																							${this.icon_menu_active ? this.icon_menu_active.value : ''}
																						</div>
																					</div>
																				{%- endif -%}
																			</a>
																		</div>
																		{% if grand_link.size > 0 %}
																			<ul class="ecom-shopify__menu-sub-menu" data-menu-size="{{grand_link.size}}" data-menu-level="{{child_link.level}}">
																				{% for grand in grand_link %}
																					<li class="ecom-shopify__menu-grand-link-item ecom-menu_item ecom-items ecom-shopify__menu-grand-link-item-type--{{grand.type}} {% if grand.active or grand.child_active %}ecom-shopify__menu-grand-link-item--active ecom-text-active{% endif %}">
																						<a class="ecom-menu_title ecom-items--text ecom-element--menu_title" href="{{grand.url}}" title="{{grand.title | escape }}">
																							{{grand.title}}
																						</a>
																					</li>
																				{% endfor %}
																			</ul>
																		{%- endif -%}
																	</li>
																{% endfor %}
															</ul>
														{% endif %}
													</li>
												{% endfor %}
											{% endif %}
										</ul>	
									</div>
								{%- endif -%}
								<div class="ecom-shopify__menu-list--wrapper ecom-flex">
									<ul class="ecom-shopify__menu-list" data-menu-layout="${this.layout}">
										{% if menu and menu.links.size %}
											{% for link in menu.links %}
												{% assign child_link = link.links %}
												<li class="ecom-shopify__menu-item ecom-shopify__menu-item-type--{{link.type}} {% if child_link.size > 0 %}ecom-shopify__menu-item--has-children{% endif %}">
													<div class="ecom-menu_item ecom-items">
														<a class="ecom-menu_title ecom-items--text ecom-element--menu_title {% if link.active or link.child_active %} ecom-shopify__menu-item--active ecom-text-active {% endif %}" href="{{link.url}}" title="{{link.title | escape }}">
															{{link.title}}
															{%- if link.levels > 0 -%}
																<div class="ecom-element--menu_icon">
																	<div class="ecom-items--icon ecom-element--menu_icon--normal">
																		${this.icon_menu ? this.icon_menu.value : ''}
																	</div>
																	<div class="ecom-items--icon ecom-element--menu_icon--active">
																		${this.icon_menu_active ? this.icon_menu_active.value : ''}
																	</div>
																</div>
															{%- endif -%}
														</a>
													</div>
													{% if child_link.size > 0 %}
														{%- capture layout -%}
															${this.data.settings.layout}
														{%- endcapture -%}
														<ul class="ecom-shopify__menu-sub-menu" data-menu-size="{{child_link.size}}" data-menu-level="{{child_link.level}}">
															{% for child in child_link %}
																{% assign grand_link = child.links %}
																<li class="ecom-shopify__menu-child-link-item ecom-shopify__menu-child-link-item-type--{{child.type}} {% if grand_link.size > 0 %}ecom-shopify__menu-child-link-item--has-children{% endif %} {% if child.active or child.child_active%} ecom-shopify__menu-child-link-item--active ecom-text-active{% endif %}" >
																	<div class="ecom-menu_item ecom-items">
																		<a class="ecom-menu_title ecom-items--text ecom-element--menu_title" href="{{child.url}}" title="{{child.title | escape }}">
																			{{child.title}}
																			{%- if child.levels > 0 -%}
																				<div class="ecom-element--menu_icon">
																					<div class="ecom-items--icon ecom-element--menu_icon--normal">
																						${this.icon_menu ? this.icon_menu.value : ''}
																					</div>
																					<div class="ecom-items--icon ecom-element--menu_icon--active">
																						${this.icon_menu_active ? this.icon_menu_active.value : ''}
																					</div>
																				</div>
																			{%- endif -%}
																		</a>
																		{% if grand_link.size > 0 and layout == 'horizontal' %}
																			{% for grand in grand_link %}
																				<a class="ecom-menu_title ecom-items--text ecom-element--menu_title" href="{{grand.url}}" title="{{grand.title | escape }}">
																					{{grand.title}}
																				</a>
																			{% endfor %}
																		{% endif %}
																	</div>
																	{% if grand_link.size > 0 and layout == 'vertical' %}
																		<ul class="ecom-shopify__menu-sub-menu" data-menu-size="{{grand_link.size}}" data-menu-level="{{child_link.level}}">
																			{% for grand in grand_link %}
																				<li class="ecom-shopify__menu-grand-link-item ecom-menu_item ecom-items ecom-shopify__menu-grand-link-item-type--{{grand.type}} {% if grand.active or grand.child_active %}ecom-shopify__menu-grand-link-item--active ecom-text-active{% endif %}">
																					<a class="ecom-menu_title ecom-items--text ecom-element--menu_title" href="{{grand.url}}" title="{{grand.title | escape }}">
																						{{grand.title}}
																					</a>
																				</li>
																			{% endfor %}
																		</ul>
																	{%- endif -%}
																</li>
															{% endfor %}
														</ul>
													{% endif %}
												</li>
											{% endfor %}
										{% endif %}
									</ul>
								</div>
							{% else %}
								<p>Select a menu to show</p>
							{%- endunless -%}
						`,
						preview: `The sample text for menu`
					}
				};
			},

			settings () {
				return [{
					params: [
						{
							type: 'picker',
							name: 'link_list',
							label: 'Pick a menu',
							options: {
								type: 'menu',
								layout:'list',
								multiple: false
							}
						},
						{
							type: 'dropdown',
							name: 'layout',
							label: 'Menu layout',
							options: {
								default: false,
								values: {
									horizontal: 'Horizontal',
									vertical: 'Vertical'
								}
							}
						},
						{
							type: 'toggle',
							label: 'Show all items by default',
							name: 'show_all_items_menu',
							options: {
								visible: (data) => {
									return data['layout'] === 'vertical';
								},
								values: {
									on: {
										label: 'Show',
										value: true
									},
									off: {
										label: 'Hide',
										value: false
									}
								}
							},
							css: false
						},
						{
							name: "tab",
							type: "tab",
							value: "normal",
							options: {
								tabs: [
									{
										name: "normal",
										title: "Normal"
									},
									{
										name:'active',
										title:'Active'
									}
								]
							},
							css: {
								isCss: false
							}
						},
						{
							type: "picker",
							label: "Icon",
							name: "icon_menu",
							options: {
								reset: false,
								type: "icon",
								multiple: false,
                                visible: (data) => {
									return data && data['tab'] === 'normal';
								},
							},
							css: {
								isCss: false
							}
						},
						{
							type: "picker",
							label: "Icon",
							name: "icon_menu_active",
							options: {
								visible: (data) => {
									return data && data['tab'] === 'active';
								},
								reset: false,
								type: "icon",
								multiple: false
							},
							css: {
								isCss: false
							}
						},
						{
							type: "line",
						},
						{
							type: 'switch',
							name: 'show_humber',
							label: 'Show menu icon on tablet and mobile',
							options: {
								values: {
									on: {
										label: 'Show',
										value: true
									},
									off: {
										label: 'Hide',
										value: false
									}
								}
							}
						},
						{
							type: "picker",
							label: "Icon",
							name: "icon_humber",
							options: {
								visible: (data) => {
									return data['show_humber'] === true;
								},
								reset: false,
								type: "icon",
								multiple: false

							},
							css: {
								isCss: false
							}
						},
					]
				}];
			},
			layout () {
				return this.data && this.data.settings && this.data.settings.layout ? this.data.settings.layout : 'horizontal';
			},
			icon_menu() {
				return this.data?.settings?.icon_menu;
			},
			icon_menu_active() {
				return this.data?.settings?.icon_menu_active;
			},
			style(){
				let style = [
					{
						group_alias: 'box',
						options: {
							group_title: "General"
						},
						modify: {
							params: [
								{
									position: 2,
									fields: [
										{
											alias: "justify-content",
											options: {
												css: {
													selector: "root .ecom-element.ecom-shopify.ecom-shopify__menu-container .ecom-shopify__menu-list[data-menu-layout='horizontal'], .ecom-shopify__menu-list--wrapper"
												},
												label: "Alignment",
											}
										}
									]
								}
							]
						}

					},
					{
						group_alias: 'text:active',
						options:{
							group_title: 'Link',
							css:{
								selector: ' .ecom-shopify__menu-item a'
							}
						},
						modify:{
							remove: {
								index: 0,
								length: 1
							},
							params: [
								{
									position: 20,
									fields: [{type: 'line'},{ alias: 'spacing'}]
								}
							]
						}
					}
				 ];
				let enableHumberIcon = {
					group_alias: 'icon',
					options: {
						group_title: 'Menu icon on tablet & mobile',
						selector: ' .ecom-menu__icon-humber'
					},
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: [
                                    {
                                        alias: 'justify-content',
                                        options: {
                                            label: 'Alignment',
                                            css: {
                                                selector: 'root .ecom-menu__icon-humber--wrapper'
                                            }
                                        }
                                    }
                                ]
                            }
                        ]
                    }
				};
				let boxMenuResponsive = {
					group_alias: 'box',
					options: {
						group_title: "Menu box on tablet & mobile",
						group_name: 'box_responsive',
						selector: ' .ecom-shopify__menu-list--mobile--wrapper .ecom-shopify__menu-list--mobile'
					},
					modify: {
						params: [
							{
								position: 0,
								fields: [
									{
										type: 'number',
										label: 'Width',
										name: 'width',
										options: {
											units: {
												"%": {
													min: 0,
													max: 100
												}
											}
										},
										css: {
											important: true
										}
									},
								]
							},
                            {
                                position: 20,
                                fields: {
                                    alias: 'spacing'    
                                }
                            }
						]
					}
				};
                let icon = {
                    group_alias: 'icon',
                    options: {
                        group_title: 'Icon',
                        group_name: 'icon__',
                        selector: ' .ecom-element--menu_icon .ecom-items--icon'
                    },
                    modify: {
                        params: [
                            {
                                position: 20,
                                fields: {   
                                    alias: 'spacing',
                                }
                            }
                        ]
                    }
                }
                style.push(icon);
				let subMenu = {
                    group_alias: 'box',
                    options: {
                        group_name: 'sub_menu',
                        group_title: 'Sub menu',
                        selector: ' .ecom-shopify__menu-list[data-menu-layout="horizontal"] .ecom-shopify__menu-item ul.ecom-shopify__menu-sub-menu'
                    },
                    modify: {
                        remove: {

                        },
                        params: [
                            {
                                position: 20,
                                fields: [
                                    {
                                        alias: 'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                }
                if(this.data.settings?.layout == 'horizontal'){
                    style.push(subMenu);
                }
				if(this.data.settings?.show_humber) {
					style.push(boxMenuResponsive);
					style.push(enableHumberIcon);
				}
				return style;
			},
			javascript() {
				return function(){
					if(!this.$el) return;
					const $el = this.$el;
					const $this = this;
					const wrapper = $el.closest('.core__row--columns');
					const menu = $el.querySelector('.ecom-shopify__menu-list--mobile');
					const iconMenu = $el.querySelector('.ecom-menu__icon-humber'),
							closeMenu = $el.querySelector('.ecom-menu-collapse-close--mobile');
					let filter_core_block = $el.closest('div.ecom-core.core__block') || '',
						filter_core_column = $el.closest('div.ecom-column.ecom-core') || '';
					if(iconMenu){
						iconMenu.addEventListener('click', openMenuResponsive);
						closeMenu.addEventListener('click', closeMenuResponsive);
						// document.addEventListener('keydown', escOutSideCloseQuickView);
					}
					if(wrapper){
						wrapper.style.zIndex = '100';
					}
					function openMenuResponsive() {
                        if(!menu) return;
						menu.parentNode.style.display = 'block';
						menu.classList.add('ecom-show');
                        //fix z-index on Editor
                        filter_core_block ? (filter_core_block.style.zIndex = '100') : '';
                        filter_core_column ? (filter_core_column.style.zIndex = '100') : '';

                        //add menu opened class to body
                        document.querySelector('body').classList.add('ecom-menu-opened');
						setTimeout(function(){
							document.addEventListener('click', clickOutSideCloseMenu);
							document.addEventListener('keydown', escCloseMenu);
						},500);

                    }
					function clickOutSideCloseMenu(e) {
                        let element = e.target;
                        do {
                            if(element == menu){
                                return;
                            }
                            element = element.parentNode;
                        }while(element);
                        if(element != menu) {
                            closeMenuResponsive();
                            document.removeEventListener('click', clickOutSideCloseMenu);
                            document.removeEventListener('keydown', escCloseMenu);
                        }
                    }
					function escCloseMenu(e) {
                        if (e.isComposing || e.keyCode === 27) {
								closeMenuResponsive();
                                document.removeEventListener('keydown', escCloseMenu);
                                document.removeEventListener('click', clickOutSideCloseMenu);
                        }
                    }
					function closeMenuResponsive() {
						menu.parentNode.style.display = 'none';
                        menu.classList.remove('ecom-show');
                        //fix z-index on Editor
                        filter_core_block ? (filter_core_block.style.zIndex = '1') : '';
                        filter_core_column ? (filter_core_column.style.zIndex = '1') : '';
                        document.querySelector('body').classList.remove('ecom-menu-opened');
						document.removeEventListener('keydown', escCloseMenu);
						document.removeEventListener('click', clickOutSideCloseMenu);
                    }
					//Hanlde collapsible menu
					let horizontal = $el.querySelector('.ecom-shopify__menu-list[data-menu-layout="horizontal"]'), parrentLink = null;
					if(horizontal) parrentLink = horizontal.querySelectorAll('.ecom-shopify__menu-item--has-children>.ecom-menu_item>.ecom-element--menu_title');
					if(parrentLink) {
							parrentLink.forEach(function(link){
								link.addEventListener('click', function(e){
									e.preventDefault();
								});
						});
					}
					function hanldeCollapsible() {
						var coll = $el.querySelectorAll(".ecom-shopify__menu-list .ecom-shopify__menu-item--has-children > .ecom-menu_item, .ecom-shopify__menu-list .ecom-shopify__menu-child-link-item--has-children > .ecom-menu_item");
						var coll1 = $el.querySelectorAll(".ecom-shopify__menu-list--mobile .ecom-shopify__menu-item--has-children > .ecom-menu_item, .ecom-shopify__menu-list--mobile .ecom-shopify__menu-child-link-item--has-children > .ecom-menu_item");
						if(!coll) return;
						var i;
						var isDefaultShowAll = "false";
						var ecom_shopify_menu = $el.querySelector(".ecom-shopify__menu-wrapper");
						if(ecom_shopify_menu && ecom_shopify_menu.dataset.showAll) {
							var isDefaultShowAll = ecom_shopify_menu.dataset.showAll;
						}
						for (i = 0; i < coll.length; i++) {
							if($this.settings.layout === 'horizontal' && !$this.isLive) {
								coll[i].addEventListener("click", function(e) {
									e.preventDefault();
									// activeCollapsible(this);
								});
							}
							else if ($this.settings.layout === 'horizontal' && $this.isLive) {
								coll[i].addEventListener("click", function(e) {
									e.stopPropagation();
									// activeCollapsible(this);
								});
							}
							else if($this.settings.layout === 'vertical' || !$this.isLive){
								if(isDefaultShowAll && isDefaultShowAll == "true") {
									activeCollapsible(coll[i]);
								}
								coll[i].addEventListener("click", function(e) {
									e.preventDefault();
									activeCollapsible(this);
								});
							}
							
							
							if(coll1[i]){
								coll1[i].addEventListener("click", function(e) {
										e.preventDefault();
										// e.target.classList.toggle('ecom-text-active');
										activeCollapsible(this);
									});
							}
							function activeCollapsible(item) {
								let content = item.nextElementSibling;
								let parent_sub_menu = null;
								if(item.classList.contains("ecom-item-active")) {
									item.classList.remove("ecom-item-active");
									if(content) {
										content.style.maxHeight = null;

										var child_menu_items = content.querySelectorAll('.ecom-menu_item');
										if(child_menu_items) {
											child_menu_items.forEach((item) => {
												var sibling_content = item.nextElementSibling;
												if(sibling_content) {
													sibling_content.style.maxHeight = null;
												}
												item.classList.remove('ecom-item-active');
											});
										}

										parent_sub_menu = item.closest('.ecom-shopify__menu-sub-menu');
										if(parent_sub_menu) {
											parent_sub_menu.style.maxHeight = parseInt(parent_sub_menu.style.maxHeight) - content.scrollHeight + "px";
										}

									}
									
								} else {
									item.classList.add("ecom-item-active");
									if (content) {
										parent_sub_menu = item.closest('.ecom-shopify__menu-sub-menu');
										if(parent_sub_menu) {
											parent_sub_menu.style.maxHeight = parseInt(parent_sub_menu.style.maxHeight) + content.scrollHeight + "px";
										}
										content.style.maxHeight = content.scrollHeight + "px";
									}
									
								}
							}
						}
					}
					hanldeCollapsible();
				};
			},
			show_all_items_menu() {
				return this.data?.settings?.show_all_items_menu;
			},
